<template>
  <div>
    <el-dialog :visible.sync="imgMngrDlgVsbl" width="70%" :before-close="handleClose">
      <div>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="上传预览" name="uploadPart">
            <thumbnail></thumbnail>
          </el-tab-pane>
          <el-tab-pane label="识别结果" name="resultPart">识别</el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import thumbnail from "@/components/Thumbnail";

export default {
  data() {
    return {
      activeName: "uploadPart"
    };
  },
  computed: {
    imgMngrDlgVsbl() {
      return this.$store.getters.imgMngrDlgVsbl;
    }
  },
  components: {
    thumbnail
  },
  methods: {
    closeDialog() {
      this.$emit("close-dialog");
    },
    handleClose(done) {
      this.$store.dispatch("setImgMngrDlgVsbl", false);
    },
    handleClick(tab, event) {
      //console.log(tab, event);
    }
  }
};
</script>


