<template>
  <div
    @click="changeChoose()"
    style="position: relative; right: 10%; margin-bottom: 10%"
    :class="[choosed?'item-choosed':'']"
  >
    <el-card :body-style="{ padding: '0px' }">
      <img :src="imgSrc" class="image" />
      <div style="padding: 14px;">
        <span>{{ imgName }}</span>
        <div class="bottom">
          <!-- <time class="time">{{ currentDate }}</time> -->
          <el-button type="text" class="button" icon="el-icon-delete" @click="removeThis"></el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  props: {
    index: null,
    imgSrc: null,
    imgName: null,
    choosed: null
  },
  data() {
    return {
      currentDate: new Date()
    };
  },
  computed: {
    selectAll() {
      return this.$store.getters.selectAll;
    }
  },
  methods: {
    removeThis(event) {
      this.$emit("remove-item", this.index);
      event.stopPropagation();
    },
    changeChoose() {
      var chooseInfo = { choosed: !this.choosed, index: this.index };
      this.$emit("choose-changed", chooseInfo);
    }
  }
};
</script>

<style scoped>
.item-choosed {
  border: 1px solid rgb(74, 173, 253);
  box-shadow: none;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  margin-right: -5px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}
</style>


