<template>
  <div>
    <el-card class="box-card" ref="boxCard">
      <div slot="header" class="clearfix">
        <el-checkbox
          v-model="selectAll"
          id="selectAllCheckBox"
          style="float:left;margin-top:-3%;"
          :disabled="checkBoxDisabled"
          @change="setSelectAll"
          :indeterminate="isIndeterminate"
        ></el-checkbox>
        <el-popover placement="bottom" title="提示" style="float:right;margin-top:-3%;">
          <i class="el-icon-question" slot="reference"></i>

        </el-popover>
      </div>
      <div class="mask" v-show="imgList.length === 0">
        <span style="font-size: 125%">通过拍照或上传添加图片</span>
      </div>
      <div class="view-container">
        <ul style="overflow: auto;">
          <li
            v-for="item in imgList"
            is="thumbnailItem"
            :key="item.index"
            :index="item.index"
            :imgSrc="item.url"
            :imgName="item.name"
            :choosed="item.choosed"
            @remove-item="removeThumbnailItem"
            @choose-changed="updateChoosed"
          ></li>
        </ul>
      </div>
    </el-card>

    <div id="button-container" style="margin-top: 10%;">
      <input
        type="file"
        ref="fileInput"
        accept="image/*"
        @change="getFile"
        style="display: none"
        multiple
      />
      <div class="row1" style="margin-bottom:8%">
        <el-button
          type="primary"
          icon="el-icon-camera"
          plain
          style="position:relative; left:-5%"
          round
        >拍照</el-button>
        <el-button
          type="primary"
          @click="$refs.fileInput.click();"
          icon="el-icon-upload2"
          plain
          style="position:relative; right: -5%;"
          round
        >上传</el-button>
      </div>
      <div class="row2">
        <el-popconfirm
          confirmButtonText="确定"
          cancelButtonText="取消"
          icon="el-icon-info"
          iconColor="red"
          title="确定要清空列表吗？"
          @onConfirm="clearList()"
          style="position: relative; left: -5%"
        >
          <el-button
            type="primary"
            icon="el-icon-delete"
            :disabled="clearButtonDisabled"
            plain
            slot="reference"
            round
          >清空</el-button>
        </el-popconfirm>
        <el-button
          type="primary"
          @click="identity"
          icon="el-icon-view"
          :disabled="identityButtonDisable"
          plain
          style="position: relative; right: -5%; margin-left:10px"
          round
        >识别</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import thumbnailItem from "./ThumbnailItem";

export default {
  data() {
    return {
      index: 1, //图片的起始index
      imgList: [], //一个item的index及图片的src和name
      choosedCount: 0, //选中的item的数量
      selectAll: false,
      isIndeterminate: false //表示checkbox是否处于不确定状态，主要用来控制样式
    };
  },
  components: {
    thumbnailItem
  },
  computed: {
    identityButtonDisable() {
      return this.choosedCount === 0 || this.imgList.length === 0;
    },
    checkBoxDisabled() {
      return this.imgList.length === 0;
    },
    clearButtonDisabled() {
      return this.imgList.length === 0;
    }
  },
  watch: {
    choosedCount: function(newVal, oldVal) {
      if (newVal === this.imgList.length && newVal !== 0) {
        this.selectAll = true;
        this.isIndeterminate = false;
      } else if (newVal === 0) {
        this.selectAll = false;
        this.isIndeterminate = false;
      } else if (newVal != this.imgList.length) {
        this.selectAll = false;
        this.isIndeterminate = true;
      }
    }
  },
  methods: {
    errorFileMessage(fileName) {
      this.$message.error(`${fileName}上传失败`);
    },
    handleInvalidFile(invalidFiles) {
      let tipMessageHtml = "<ul>";

      for (let i = 0; i < invalidFiles.length; i++) {
        if (invalidFiles[i].error === "form") {
          tipMessageHtml +=
            "<li style='list-style: none;'>" +
            `${invalidFiles[i].name}上传失败，格式错误。` +
            "</li>";
        } else {
          tipMessageHtml +=
            "<li style='list-style: none;'>" +
            `${invalidFiles[i].name}上传失败，大小不能超过5M。` +
            "</li>";
        }
      }

      tipMessageHtml += "</ul>";

      this.$message.error({
        message: tipMessageHtml,
        dangerouslyUseHTMLString: true,
        duration: 0,
        showClose: true
      });
    },
    getFile(event) {
      const _this = this;
      const files = event.target.files;
      let invalidFiles = []; //存放非法文件的名称，即不是指定格式的文件

      for (let i = 0; i < files.length; i++) {
        let fileName = files[i].name;
        let pattern = new RegExp("^.*?.(jpeg|jpg|png)$", "gi");
        //let pattern = /^.*?\.(jpeg|jpg|png)$/;

        if (pattern.test(fileName) === false) {
          invalidFiles.push({ name: fileName, error: "form" });
          continue;
        }

        if (files[i].size > 5242880) {
          invalidFiles.push({ name: fileName, error: "size" });
          continue;
        }

        let _imgList = this.imgList;
        let file = files[i];

        const fileReader = new FileReader();
        fileReader.onload = function() {
          let fileUrl = fileReader.result;

          if (fileUrl !== null) {
            _imgList.push({
              index: _this.index++,
              file: file,
              url: fileUrl,
              name: fileName,
              choosed: false
            });
          } else {
            console.log("error");
          }
        };

        fileReader.readAsDataURL(files[i]);
      }

      if (invalidFiles.length !== 0) this.handleInvalidFile(invalidFiles);
    },
    removeThumbnailItem(index) {
      let target = null;

      for (let i = 0; i < this.imgList.length; i++) {
        if (this.imgList[i].index === index) {
          target = i;
          break;
        }
      }

      if (target !== null) {
        this.imgList.splice(target, 1);
        this.choosedCount--;
      }
    },
    updateChoosed(chooseInfo) {
      let target = null;
      for (let i = 0; i < this.imgList.length; i++) {
        if (this.imgList[i].index === chooseInfo.index) {
          this.imgList[i].choosed = chooseInfo.choosed;
          this.choosedCount += chooseInfo.choosed ? 1 : -1;
          break;
        }
      }
    },
    clearList() {
      this.imgList.splice(0, this.imgList.length);
      this.index = 1;
      this.choosedCount = 0;
    },
    setSelectAll(value) {
      this.isIndeterminate = false;
      if (this.selectAll) {
        for (let i = 0; i < this.imgList.length; i++) {
          this.imgList[i].choosed = true;
        }
        this.choosedCount = this.imgList.length;
      } else {
        for (let i = 0; i < this.imgList.length; i++) {
          this.imgList[i].choosed = false;
        }
        this.choosedCount = 0;
      }
    },
    identity() {
      const loading = this.$loading({
        lock: true,
        text: "识别中",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
        target: ".el-card"
      });

      let _imgList = this.imgList;
      let _choosedCount = this.choosedCount;
      let currentCount = 0;

      _imgList.forEach((item, index) => {
        if (item.choosed) {
          this.$store
            .dispatch("identity", { file: item.file, base64: item.url })
            .then(res => {
              console.log(res.data);
            })
            .catch(err => {
              console.log(err);
            })
            .finally(() => {
              //识别以后要把这张图从列表中去掉
              let _index = _imgList.indexOf(item);
              _imgList.splice(_index, 1);
              this.choosedCount--;

              if (++currentCount === _choosedCount) {
                loading.close();
              }
            });
        }
      });
    }
  }
};
</script>

<style scoped>
.view-container {
  height: 300px;
  width: 100%;
  overflow: scroll;
}

.mask {
  position: absolute;
  top: 30%;
  left: 0px;
  right: 0px;
  color: grey;
}
</style>


