<template>
  <div>
    <el-tabs
      v-model="activeName"
      @tab-click="handleClick"
      style="position:absolute; width: 80%; left: 10%; top: 10%"
    >
      <el-tab-pane label="上传预览" name="upload">
        <thumbnail></thumbnail>
      </el-tab-pane>
      <el-tab-pane label="识别结果" name="result">
        <result></result>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import imgManager from "@/components/ImgManager";
import thumbnail from "@/components/Thumbnail";
import result from '@/components/Result';

export default {
  data() {
    return {
      activeName: "upload"
    };
  },
  components: {
    imgManager,
    thumbnail,
    result
  },
  mounted() {
    //this.getCompetence();
  },
  computed: {},
  methods: {
    handleClick(tab, event) {}
  }
};
</script>

<style scoped>
.el-carousel__item h3 {
  color: #475669;
  font-size: 18px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}

.item {
  margin-top: 10px;
  margin-right: 40px;
}
</style>


